$primaryColor: #396afc;
$infoColor: #1976d2;
$successColor: #43a047;
$warningColor: #ffa000;
$errorColor: #d32f2f;

.message {
    display: flex;
    align-items: center;
}
.icon {
    margin-right: 10px;
}
.content {
    font-size: 26px;
    line-height: 34px;
}
.error {
    background-color: $errorColor !important;
}

.info {
    background-color: $infoColor !important;
}

.success {
    background-color: $successColor !important;
}

.warning {
    background-color: $warningColor !important;
}
