.header {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 7rem;
    background: url('./bg.png') no-repeat #fff;
    background-size: cover;
    z-index: 10;
    h1 {
        font-size: 1.3rem;
    }
}

.tip {
    position: absolute;
    width: 92%;
    padding: 0.5rem;
    background: #fff;
    top: 5.3rem;
    z-index: 999;
    left: 4%;
    box-shadow: 0 0.05rem 0.1rem rgba(0, 0, 0, 0.3);
    border-radius: 0.1rem;
    font-size: 0.7rem;
    display: flex;
    align-items: center;
}

.info {
    padding-left: 10px;
    font-weight: bold;
    font-size: 1rem;
}

.main {
    padding-top: 11rem;
    width: 92%;
    padding-bottom: 4rem;
    margin: 0 auto;
    :global(.MuiTypography-h5) {
        font-size: 1rem;
    }
    :global(.MuiFormControl-fullWidth) {
        margin: 8px 0;
    }
}
